/*Variables*/
/* Search section styling */
.search-container {
  width: 100%;
  align-items: center;
  padding-top: 4.5em;
  padding-bottom: 5em;
  text-align: center;
  background: url(/static/media/LA-landscape.ccc2aab6.jpg);
  background-position: 5% 40%;
  background-repeat: no-repeat;
  background-size: cover;
  color: white; }
  .search-container h1 {
    margin: 0 auto; }

.search {
  display: flex;
  justify-content: space-around;
  flex-flow: wrap;
  flex-direction: row;
  text-align: left; }
  .search h3 {
    font-size: .9em; }

.keyword-search, .location-search {
  width: 200px; }

.search-input, .location-input {
  width: 100%;
  padding: 1em;
  border-radius: 0.25em;
  border: 1px white solid;
  font-family: "Open Sans"; }

.keyword-search, .location-search, .search-btn-content-container {
  margin: 10px; }

.search-btn {
  width: 100%;
  padding: 1em;
  border-radius: 0.4em;
  border: 1px solid #ffd500;
  background: #ffd500;
  color: #111;
  font-family: "Open Sans"; }

.search-btn-content-container {
  margin: 10px;
  width: 75px; }

@media screen and (max-width: 490px) {
  .search-btn-content-container {
    padding: 0 80px; } }

@media screen and (min-width: 700px) {
  .search {
    margin: auto;
    width: 730px; } }

/*Variables*/
/* Recent job postings styling */
.recent-postings-container {
  width: 100%;
  margin-top: 2em; }
  .recent-postings-container ul {
    list-style: none;
    margin-right: 40px; }
    .recent-postings-container ul li {
      margin-bottom: 10px; }

.recent-postings-title {
  padding-left: 1em; }

.posting-card {
  display: flex;
  align-items: center;
  align-content: space-around;
  width: calc(100% - 2em);
  height: 200px;
  margin: 0em 2em;
  border: 1px solid gray;
  border-radius: 0.2em;
  box-shadow: 8px 8px 6px -6px gray; }

.left-posting, .middle-posting, .right-posting {
  padding: 0 15px; }

.posting-content {
  display: flex;
  flex-direction: row;
  align-items: center; }

.middle-posting {
  height: 100px;
  display: flex;
  flex-direction: column;
  overflow-y: hidden; }

h4 {
  margin: 0.3em 0;
  padding: 0; }

#posting-img {
  width: 90%;
  padding: 0.5em;
  border: 0.5px solid gray; }

.right-posting {
  display: flex;
  flex-direction: center;
  height: 100%;
  justify-content: flex-end; }
  .right-posting #view-more-btn {
    font-family: "Open Sans";
    font-weight: bold;
    padding: 1em 2em;
    background: #ffd500;
    border-radius: 0.4em;
    border: 1px #ffd500 solid; }
    .right-posting #view-more-btn:hover {
      cursor: not-allowed; }

@media screen and (max-width: 550px) {
  .recent-postings-container ul {
    padding: 0; }
    .recent-postings-container ul .posting-card {
      height: 250px; }
    .recent-postings-container ul .posting-content {
      flex-direction: column;
      padding: 6px; }
      .recent-postings-container ul .posting-content p {
        text-align: center; }
      .recent-postings-container ul .posting-content .left-posting h3 {
        text-align: center; }
      .recent-postings-container ul .posting-content .middle-posting h4 {
        text-align: center; } }

@media screen and (max-width: 450px) {
  .recent-postings-container ul {
    padding: 0; }
    .recent-postings-container ul .posting-content {
      padding: 20px; } }

/*Variables*/
.filter-bar-container {
  height: 100vh;
  /* Full-height: remove this if you want "auto" height */
  width: 190px;
  /* Set the width of the sidebar */
  top: 10em;
  padding-left: 1em;
  background-color: #ddd; }

.filter-options {
  display: flex;
  flex-direction: row; }

.filter-criteria {
  padding: 0.5em; }

.filters-title {
  padding: 0.5em;
  font-weight: 900; }

.distance-selector {
  margin-top: 0.25em;
  padding: 0.75em;
  border-radius: 0.25em;
  border: 1px gray solid; }

@media screen and (max-width: 765px) {
  .filter-bar-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around; } }

@media screen and (max-width: 450px) {
  .filter-bar-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr minmax(41px, auto); }
    .filter-bar-container .filters-title {
      height: 25px;
      grid-column: 1; }
    .filter-bar-container .distance {
      grid-column: 1;
      grid-row: 2; }
    .filter-bar-container .employment-type {
      grid-column: 2;
      grid-row: 2; }
    .filter-bar-container .experience-level {
      grid-column: 3;
      grid-row: 2; } }

/* Organization cards styling */
.organization-cards-container {
  display: flex;
  flex-wrap: wrap;
  padding: 1em 0em;
  width: 100%; }

.organization-card {
  width: 33%;
  padding: 1em 1em;
  box-sizing: border-box;
  text-align: center; }

.organization-card-content {
  margin: 2em 7%;
  padding: 3em 0em;
  border: 0.4px solid gray;
  border-radius: 0.2em;
  box-shadow: 4px 4px 3px -3px gray; }

#org-img {
  height: 10em; }

#org-title {
  margin: 0 10%;
  width: 80%;
  margin-top: 1em; }

#org-jobs-available {
  margin-top: 1em;
  text-decoration: underline; }

#org-view-more-btn {
  width: 55%;
  padding: 0.7em;
  margin-top: 1.5em;
  border-radius: 0.4em;
  border: 1px #ffd100 solid;
  background: #ffd100;
  font-weight: bold;
  opacity: 0.75; }

#org-view-more-btn:hover {
  opacity: 1; }

/*Variables*/
/* Contact banner styling */
.contact-banner {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  height: 40vh;
  background-image: -webkit-linear-gradient(rgba(42, 39, 39, 0.3), rgba(42, 39, 39, 0.3)), url(/static/media/matt-collamer-555626-unsplash.f6d7a91f.jpg);
  background-image: linear-gradient(rgba(42, 39, 39, 0.3), rgba(42, 39, 39, 0.3)), url(/static/media/matt-collamer-555626-unsplash.f6d7a91f.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-size: 110%;
  background-position: 15% 40%; }
  .contact-banner .contact-title {
    padding-left: 75px;
    width: 20%;
