.contact-content-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 3.5em;
  padding-bottom: 2em; }

/* Department information styling */
.department-information-container, .contact-form-container {
  flex-growth: 1; }

/* Contact form styling */
.contact-input {
  width: 90%;
  margin-bottom: 2em;
  padding: 0.75em 1.75em;
  border-radius: 0.5em;
  border: 1px lightgray solid; }

#message {
  height: 15em; }

#send-btn {
  font-family: "Open Sans";
  font-weight: bold;
  font-size: 1.2em;
  margin: 0 3.5em 30px 0;
