  background: #ffd500;
  border-radius: 0.4em;
  border: 1px #ffd500 solid; }

.department-information-container {
  text-align: center; }

.department-info {
  margin: 1em 0em;
  padding: 0;
  font-weight: 100; }

#department-address-continued {
  margin: 0;
  padding: 0; }

#web-link {
  color: #266294; }

#county-img {
  height: 13em;
  margin-top: 2em; }

@media screen and (max-width: 770px) {
  .contact-banner {
    height: 35vh;
    background-size: 140%;
    background-position: 35% 30%; }
  .contact-title {
    padding-left: 30px;
